// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage, showErrorMessage, showSuccessMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import DialogSettingColumn from '@/components/common/DialogCustomizeColumns/DialogSettingColumn';
import { getUrlKey } from '@/utils/common';
import { mixDefaultAndCustomColumns } from '@/utils/publicFunction';
// 导入自定义列配置有关接口
import {
  // 保存自定义列配置
  saveCustomColumnSettingAPI,
  // 获取自定义列配置
  getCustomColumnSettingAPI
} from '@/api/customizeColumn';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MONITORING
} from '@/utils/constantsFile';
// 导入所需接口
import { GetList, DeviceOEE } from '@/api/equipmentmonitoring';
import { exportDeviceOEE } from '@/api/export';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    DialogSettingColumn
  },
  props: {
    type: Number
  },
  data() {
    return {
      EQUIPMENT_MONITORING: EQUIPMENT_MONITORING,
      BTN_TEXT: BTN_TEXT,
      deviceName: '',
      lineOptions: [],
      groupOptions: [],
      dateTime: '',
      groupId: '',
      lineId: '',
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'date',
          title: '日期',
          visible: true,
          width: '160',
          align: 'center',
          fixed: 'left',
          formatter: ['formatDate', 'yyyy-MM-dd']
          // sortable: true
        },
        {
          field: 'workshop',
          title: EQUIPMENT_MONITORING.WORKSHOP_NAME,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'productionLine',
          title: EQUIPMENT_MONITORING.LINE_NAME,
          visible: true,
          width: '160',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: EQUIPMENT_MONITORING.DEVICE_NAME,
          visible: true,
          width: '160',
          align: 'center'
        },
        {
          field: 'runTimeHour',
          title: EQUIPMENT_MONITORING.UNNING_TIME,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'freeTimeHour',
          title: EQUIPMENT_MONITORING.FREE_TIME,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'warningCount',
          title: '告警次数',
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'warnTimeHour',
          title: EQUIPMENT_MONITORING.ALARM_TIME,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'offlineTimeHour',
          title: EQUIPMENT_MONITORING.OFFLINE_TIME,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'timeActivation',
          title: EQUIPMENT_MONITORING.TIME_RATE,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'performanceActivation',
          title: EQUIPMENT_MONITORING.PERFORMANCE_RATE,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'yield',
          title: EQUIPMENT_MONITORING.YIELD_RATE,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: 'oee',
          title: EQUIPMENT_MONITORING.OEE,
          visible: true,
          width: '160',
          align: 'center'
          // sortable: true
        },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          visible: true,
          width: '50',
          align: 'center',
          fixed: 'right',
          slots: {
            header: ({ column }) => {
              return [
                <span>
                  <i
                    onClick={() => this.showCustomColumn()}
                    class="el-icon-s-tools set-btn"
                  ></i>
                </span>
              ];
            }
          }
        }
      ],

      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        }
      },
      // 保存自定义之后的列配置
      savedCustomColumns: [],
      // 是否显示自定义列对话框
      isSettingCustomDialogShow: false
    };
  },
  watch: {
    groupId(val) {
      this.getLine();
      this.lineId = '';
    }
  },
  created() {
    this.getCustomColumn(22);
    // 调用列表查询接口
    // this.findList();
    // 车间分组下拉数据
    this.getGroup();
    this.getLine();
    this.findList();
  },
  computed: {
    // 表格使用的表头配置
    myTableColumns: function() {
      if ((Array.isArray(this.savedCustomColumns)) && this.savedCustomColumns.length > 0) {
        return this.savedCustomColumns;
      } else {
        return this.tableColumns;
      }
    }
  },
  methods: {
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      var Id = '';
      if (this.lineId !== '') {
        Id = this.lineId;
      } else {
        Id = this.groupId;
      }
      var startTime = '';
      var endTime = '';
      if (this.dateTime[0] !== undefined) {
        startTime = this.dateTime[0] + ' 00:00:00';
        endTime = this.dateTime[1] + ' 23:59:59';
      }
      try {
        this.tableLoadings = true;
        const res = await DeviceOEE({
          size: this.tablePages.pageSize,
          current: this.tablePages.currentPage,
          groupId: Id,
          deviceName: this.deviceName,
          startTime: startTime,
          endTime: endTime
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    /**
     * @description: 获取车间数据
     * @param obj ztree对象
     * @return: 无
     * @author: CH
     */
    async getGroup() {
      try {
        this.tableLoading = true;
        const res = await GetList(
          {},
          {
            conditionJson1: [
              {
                column: 'groupType', // 参数名
                operator: 1, // 并且
                ptype: 'int', // 参数类型
                type: 'eq', // 包含
                value: 2 // 参数值
              }
            ]
            // operator: this.searchData.groupOperator,
            // conditionJson2: this.searchData.conditionArr2
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取产线数据
     * @param obj ztree对象
     * @return: 无
     * @author: CH
     */
    async getLine() {
      var data = {
        conditionJson1: [
          {
            column: 'groupType', // 参数名
            operator: 1, // 并且
            ptype: 'int', // 参数类型
            type: 'eq', // 包含
            value: 3 // 参数值
          }
        ]
      };
      if (this.groupId !== '') {
        data.conditionJson1.push({
          column: 'parentId', // 参数名
          operator: 1, // 并且
          ptype: 'int', // 参数类型
          type: 'eq', // 包含
          value: this.groupId // 参数值
        });
      }
      try {
        this.tableLoading = true;
        const res = await GetList({}, data);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.lineOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.groupId = '';
      this.lineId = '';
      this.deviceName = '';
      this.dateTime = '';
      this.onTablePageChanges(1, 10);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      this.tableLoadings = true;
      var Id = '';
      if (this.lineId !== '') {
        Id = this.lineId;
      } else {
        Id = this.groupId;
      }
      var startTime = '';
      var endTime = '';
      if (this.dateTime[0] !== undefined) {
        startTime = this.dateTime[0] + ' 00:00:00';
        endTime = this.dateTime[1] + ' 23:59:59';
      }

      try {
        const params = {
          groupId: Id,
          deviceName: this.deviceName,
          startTime: startTime,
          endTime: endTime
        };
        const res = await exportDeviceOEE(params);
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
          this.tableLoadings = false;
        } else {
          this.tableLoadings = false;
          this.$options.filters.setExportFunc(res, '设备OEE统计报表.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
    * @description: 切换显示自定义列模态框
    * @param 无
    * @return: void
    * @author: ZJM
    */
    showCustomColumn() {
      this.isSettingCustomDialogShow = !this.isSettingCustomDialogShow;
    },
    /**
     * @description: (自定义列对话框的自定义事件) 点击“保存”按钮，调用接口
     * @param {number} cssType 同一路由下，区分不同页面的标志
     * @return promise
     * @author: cyb
     */
    async handleSaveColumnSetting(submitColumnSetting, cssType = this.cssType) {
      const loading = this.$loading({
        body: true,
        lock: true,
        text: '自定列配置提交中...'
      });

      try {
        // 获取路由的id
        // const { id } = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');

        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const header = {
          account,
          cssType: 22,
          menuId: id
        };

        const res = await saveCustomColumnSettingAPI(
          header,
          submitColumnSetting
        );

        if (res.code === '0000') {
          loading.close();
          showSuccessMessage(res.message);
          this.refreshTableColumns();
        } else {
          loading.close();
          showErrorMessage(res.message || '操作失败！');
        }
      } catch (error) {
        console.log(error);
        loading.close();
        showErrorMessage(error + '666');
      } finally {
        // 关闭对话框
        this.isSettingCustomDialogShow = false;
      }
    },
    /**
     * @description: 获取自定义列配置
     * @param 无
     * @return void
     * @author: cyb
     */
    async getCustomColumn(cssType) {
      try {
        // 调接口获取当前页面的menuId
        // const urlInfo = await getMenuIdForUrlAPI({ url: this.$route.path });
        const id = getUrlKey('id');
        // 获取用户名
        const account = window.localStorage.getItem('account').replace(/"/g, '');
        const query = {
          account,
          cssType: cssType,
          menuId: id
        };
        const res = await getCustomColumnSettingAPI(query);
        // 如果有返回配置，则将默认表头和自定义配置混合
        if (Array.isArray(res) && res.length > 0) {
          const arr = mixDefaultAndCustomColumns(res, this.tableColumns);
          if (this.savedCustomColumns.length > 0) {
            this.savedCustomColumns.splice(0);
          }
          this.savedCustomColumns.push(...arr);
        }
      } catch (error) {
        showErrorMessage(error);
      }
    },
    /**
     * @description: 刷新表格表头和表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableColumns() {
      this.getCustomColumn(22);
      // 刷新表格
      this.refreshTableData();
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData() {
      // 如果搜索条件存在，则使用搜索条件进行查询
      // if (this.savedSearchCondition.length > 0) {
      // this.getTableData(...this.savedSearchCondition);
      // this.findTableList();
      // } else {
      // 否则，进行无条件的查询
      // this.getTableData();
      this.findList();
      // }
    }
  }
};
