// 引入组件
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { BTN_TEXT, EQUIPMENT_MONITORING } from '@/utils/constantsFile';
import utilizationRateTable from '@/views/report/EquipmentMonitoring/utilizationRateTable.vue';
import alarmStatistics from '@/views/report/EquipmentMonitoring/alarmStatistics.vue';
import multiDeviceComparison from '@/views/report/EquipmentMonitoring/multiDeviceComparison.vue';
import efficiencyStatistics from '@/views/report/EquipmentMonitoring/efficiencyStatistics.vue';
export default {
  name: 'EquipmentMonitoring',
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      active: 0,
      activeN: 0,
      type: 2,

      // 默认引入的组件名称
      currentView: 'utilizationRateTable',
      tabs: [
        {
          name: EQUIPMENT_MONITORING.STATISTICS_OF_CROP_MOVEMENT_RATE,
          views: 'utilizationRateTable'
        },
        {
          name: EQUIPMENT_MONITORING.ALARM_STATISTICS,
          views: 'alarmStatistics'
        },
        {
          name: '设备OEE统计',
          views: 'efficiencyStatistics'
        }
        // {
        //   name: EQUIPMENT_MONITORING.MULTI_DEVICE_DATA_COMPARISON,
        //   views: 'multiDeviceComparison'
        // }
      ],
      selectTabs: [
        {
          type: 2,
          name: EQUIPMENT_MONITORING.WORKSHOP
        },
        {
          type: 3,
          name: EQUIPMENT_MONITORING.Line
        },
        {
          type: 99,
          name: EQUIPMENT_MONITORING.DEVICE
        }
      ]
    };
  },
  created() {
    // 调用列表查询接口
  },
  methods: {
    // 按钮触发事件
    buttonClickEvent(type) {
      console.log(type);
      // if (type === 'exit') {
      //   this.$emit('closePage');
      //   return;
      // }
      this[`${type}`]();
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
      if (i === 0) {
        this.activeN = 0;
        this.type = 2;
      }
    },
    /**
     * @description: 切换设备tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggleN(i, type) {
      this.activeN = i;
      this.type = type;
    },
    refresh() {
      if (this.active === 0) {
        this.$refs.utilizationRateTable.findList();
      } else if (this.active === 1) {
        this.$refs.alarmStatistics.findList();
      } else if (this.active === 2) {
        this.$refs.efficiencyStatistics.findList();
      }
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        this.$refs.utilizationRateTable.exportTable();
      } else if (this.active === 1) {
        this.$refs.alarmStatistics.exportTable();
      } else if (this.active === 2) {
        this.$refs.efficiencyStatistics.exportTable();
      }
    }
  },
  components: {
    // 加载未审核组件
    HeaderTitle,
    HeadOperationItem,
    utilizationRateTable,
    alarmStatistics,
    multiDeviceComparison,
    efficiencyStatistics
  }
};
